import React from "react"
import { Container, Row, Col, Image } from 'react-bootstrap'
import PropTypes from 'prop-types';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faLinkedinIn, faGithub, faTwitter, faSlack, faInstagram, faFacebookF } from '@fortawesome/free-brands-svg-icons'

const SocialMediaFooter = () => {
    return (
        <ul className="list-inline social-buttons">
            <li className="list-inline-item">
                <a href="#" className="twitter-btn">
                    <FontAwesomeIcon icon={faTwitter} />
                </a>
            </li>
            <li className="list-inline-item">
                <a href="" className="instagram-btn">
                    <FontAwesomeIcon icon={faInstagram} />
                </a>
            </li>
            <li className="list-inline-item">
                <a href="#" className="facebook-btn">
                    <FontAwesomeIcon icon={faFacebookF} />
                </a>
            </li>
            <li className="list-inline-item">
                <a href="#" className="linkedin-btn"><FontAwesomeIcon icon={faLinkedinIn} /></a>
            </li>
        </ul>
    )
}

export default SocialMediaFooter